.number-list {
	counter-reset: item;
	list-style: none;
	li {
		position: relative;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0; }
		&:before {
			position: absolute;
			left: -26px;
			content: counter(item) ') ';
			counter-increment: item;
			font-family: $R;
			font-size: 15px;
			line-height: 25px;
			color: $color-text; } } }
.input-error {
