.checkbox {
	input {
		display: none;
		&:checked + label:after {
			display: block; } }
	label {
		display: block;
		max-width: 458px;
		position: relative;
		font-family: $R;
		font-size: 13px;
		line-height: 16px;
		color: #8E8E8E;
		padding-left: 27px;
		&:before {
			position: absolute;
			content: '';
			width: 16px;
			height: 16px;
			top: 0;
			left: 0;
			border: 1px solid #005AA5;
			border-radius: 50%; }
		&:after {
			display: none;
			position: absolute;
			content: '';
			top: 5px;
			left: 5px;
			width: 8px;
			height: 8px;
			background: #005AA5;
			border-radius: 50%; } } }
